import {
  useColorScheme,
  Colors,
  useResizeContext,
  useIsMobile,
  CommonButton,
} from '@blacktokki/core';
import React from 'react';
import { TouchableOpacity } from 'react-native';

export default function HeaderLeft({
  icon,
  active,
  onPress,
}: {
  icon: JSX.Element;
  active: boolean;
  onPress: () => void;
}) {
  const isMobile = useIsMobile();
  const theme = useColorScheme();
  const windowType = useResizeContext();
  if (windowType === 'portrait' && active)
    return (
      <TouchableOpacity onPress={onPress} disabled={!isMobile}>
        <CommonButton
          title={''}
          color={isMobile ? Colors[theme].buttonBackgroundColor : Colors[theme].hoverColor}
          onPress={onPress}
          disabled={isMobile}
          style={{ width: 32, height: 32, margin: 16, paddingVertical: 1, paddingHorizontal: 4 }}
        >
          {icon}
        </CommonButton>
      </TouchableOpacity>
    );
  return null;
}
